.portfolio h3 {
    font-size: 16px;
    text-transform: uppercase;
    color: rgb(236, 184, 7);
    margin: 0;
    padding: 0;
}

.portfolio h2 {
    font-size: 48px;
    color: white;
    margin-bottom: 48px;
    margin-top: 0;
}

.portfolio {
    background-color: #1d809f;
    padding-top: 120px;
    padding-bottom:120px;
    text-align: center;
}

.portfolio > .row {
    display: flex;
    justify-content: space-around;
}

.portfolio > .row span {
    background-color: white;
    width: 112px;
    height: 112px;
    display: inline-block;
    border-radius: 50%;
    color: #1d809f;
    font-size: 36px;
    line-height: 120px;
}

.portfolio > .row h4 {
    margin-bottom: 8px;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    color: inherit;
    font-size: 24px;
    color: white;
}

.portfolio > .row p {
    margin-bottom: 8px;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    color: inherit;
    font-size: 16px;
    color: white;
}

p {
    margin-left: 10px;
    margin-right: 10px;
}